@font-face {
    font-family: 'Streetline';
    src: local('Streetline'), url(../fonts/streetline.otf) format('truetype');
}

* {
    margin: 0;
    padding: 0;
}

#login-container {
    height: fit-content;
    padding: 20px 0 20px 0;
    width: 450px;
    background-color: #373442;
    border-radius: 10px;
}

#error {
    font-family: "Streetline";
    color: rgb(255, 87, 87);
    font-size: 24px;
}

#form p {
    font-family: "Streetline";
    color: white;
}

#no-account {
    margin-top: 20px;
}

body {
    background-color: #282632;
}

#container {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

#logo-name-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
}

#form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#email-form p, #password-form p {
    color: white;
    font-family: "Streetline";
}


#email-form input, #password-form input, #name-form input, #surname-form input, #password-form-repeat input {
    padding: 5px 5px 5px 5px;
    border-radius: 3px;
    border: none;
    font-size: 18px;
    width: 250px;
}

#surname-form {
    margin: 15px;
}

#password-form {
    margin: 15px;
    display: flex;
    flex-direction: column;
}

#submit {
    margin: 10px;
    padding: 5px 10px 5px 10px;
    width: 100px;
    border: none;
    border-radius: 3px;
    transition: 0.2s;
}

#submit:hover {
    cursor: pointer;
    background-color: #c6c6c6;
}

textarea:focus, input:focus{
    outline: none;
}
#brand-logo {
    height: 70px;
}

#brand-text, a {
    font-family: "Streetline";
    color: white;
}